body.noscroll {
  overflow: hidden;
  height: 100vh;
}
.site-nav-right a.ghost-hunter-search {
  margin-right: 1rem;
  color: white;
  opacity: .8;
}
.site-nav-right a.ghost-hunter-search:hover {
  opacity: 1;
  text-decoration: none;
}
section.ghost-hunter {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  z-index: 10000;
  overflow-y: scroll;
  padding: 2rem;
}
section.ghost-hunter.visible {
  display: block;
}
section.ghost-hunter .close {
  position: absolute;
  top: 20px;
  right: 20px;
  height: 30px;
  width: 30px;
  display: block;
  cursor: pointer;
}
section.ghost-hunter .close:before,
section.ghost-hunter .close:after {
  top: 15px;
  right: 0;
  width: 30px;
  height: 1px;
  content: "";
  position: absolute;
  display: block;
  background: #555555;
  opacity: .8;
  transform: rotate(45deg);
}
section.ghost-hunter .close:after {
  transform: rotate(-45deg);
}
section.ghost-hunter form .form-group {
  align-self: stretch;
  display: flex;
}
section.ghost-hunter form input {
  display: block;
  padding: 10px;
  width: 100%;
  border: 1px solid #dae2e7;
  color: #738a94;
  font-size: 1.8rem;
  line-height: 1em;
  font-weight: 400;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
  border-radius: 5px;
  transition: border-color .15s linear;
  -webkit-appearance: none;
}
section.ghost-hunter #results p.number {
  margin: 1rem 0;
}
section.ghost-hunter #results h2 {
  margin: 1em 0 .5em;
}
section.ghost-hunter #results h2 .small {
  font-size: 1.5rem;
  margin-left: 1rem;
  color: #555555;
}
